.sidebarObject {

    &_title {
        font-weight: 700;
        box-shadow: 2px 2px 2px 2px lightgray;

        &:hover {
            cursor: pointer;
        }

        &-selected {
            font-weight: 700;
            box-shadow: 2px 2px 2px 2px lightgray;
            background-color: lightgray;

            &:hover {
                cursor: pointer;
            }
        }
    }

    &_list {
        height: 0;
        overflow: hidden;
        transition: height 1s;
        transition-timing-function: linear;
    }

    &_mineral {
        display: flex;
        border: 1px solid black;
        border-radius: 0.5rem;
        padding: 0.5rem;

        &:hover {
            cursor: pointer;
        }

        & > img {
            height: 1.5rem;
        }
    }
}

.selected {
    height: 100%;
}